$main-color: beige;
$text-color: rgb(77, 10, 66);
$background-color: #0d1a26;

#movie-card {
  height: 600px;
  margin-top: 25px;
}

#movie-image {
  max-width: 100%;
  height: 375px;
}

#movie-title {
  font-size: 28px;
  height: 100px;
}

#movie-description {
  font-size: 18px;
  height: 25px;
  color: black;
}

#open-button {
  font-size: 18px;
  background-color: $main-color;
  color: $text-color;
  border: none;
  margin-top: 8px;
  padding: 6px;
  border-radius: 20%;
}